.container {
    margin-top: 10px;
    display: flex;
}

.info {
    margin: 0;
    padding-left: 10px;
}

.add_details {
    margin-top: 10px;
    margin-bottom: 10px;
    border-top: 1px solid;
    border-bottom: 1px solid;
}

h3 {
    margin: 2px;
}

.navigation {
    display: flex;
    flex-direction: column;
    height: 50px;
    margin-top: 0;
    margin-bottom: 5px;
}

.navigation a {
    padding: 5px;
    border-radius: 4px;
    text-decoration: none;
    color: rgb(30, 131, 114);
}

.navigation a:hover,
.navigation a:focus {
    color: white;
    background-color: rgb(30, 131, 114);
}

.navigation a.active {
    color: white;
    background-color: rgb(30, 131, 114);
}